<template>
  <div class="template-container">
    <div class="template-item-wrap">
      <!-- 时间选择器 begin -->
      <div class="temp-item-inlineblock">
        <el-date-picker @change="pickerDateChange" v-model="pickerDate" type="daterange" align="left" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
        </el-date-picker>
      </div>
      <!-- 时间选择器 end  -->
      <div class="temp-item-inlineblock">
        <el-select v-model="adsParams.system_status" placeholder="系统状态" style="width:180px;">
          <el-option v-for="item in ad_system_status" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-select v-model="adsParams.campaign_type" placeholder="请选择" style="width:180px;">
          <el-option v-for="item in campaign_typeList" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock" style="width:180px;">
        <el-select v-model="value" placeholder="所属计划">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock" style="width:120px;">
        <el-input v-model="adsParams.ad_name" placeholder="广告名"></el-input>
      </div>
      <div class="temp-item-inlineblock" style="width:120px;">
        <el-input v-model="adsParams.adgroup_id" placeholder="广告组 ID"></el-input>
      </div>
      <div class="temp-item-inlineblock" style="width:120px;">
        <el-input v-model="adsParams.campaign_id" placeholder="推广计划 ID"></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="searchList">查询</el-button>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="exportExcel">导出为excel</el-button>
      </div>
      <div class="temp-item-inlineblock temp-item-right">
        <el-button type="primary" @click="newAd">新建广告</el-button>
      </div>

      <div class="temp-item-block">

        <el-button type="primary" @click="handleAdListRemove">删除</el-button>

        <el-dropdown split-button type="primary" @click="handleBatchChangeStatus" @command="handleCommand">
          修改状态
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item :command="key" :key="key" v-for="(val, key) in configured_status_list">{{ val }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <div class="table-wrap" v-loading="loading">
      <el-table show-overflow-tooltip ref="multipleTable" @selection-change="handleSelectionChange" :data="listData" style="width: 100%" :default-sort="{prop: 'date', order: 'descending'}">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column prop="ad_id" label="ID" sortable width="180">
        </el-table-column>
        <el-table-column prop="ad_name" label="广告名称" sortable width="180">
        </el-table-column>
        <el-table-column prop="adcreative.adcreative_name" label="关联创意" sortable width="180">
        </el-table-column>
        <!-- <el-table-column prop="campaign_id" label="推广计划" sortable width="180">
        </el-table-column> -->
        <el-table-column prop="impression" label="曝光量" sortable>
        </el-table-column>
        <el-table-column prop="click" label="点击量" sortable>
        </el-table-column>
        <el-table-column prop="ctr" label="点击率" sortable>
        </el-table-column>
        <el-table-column prop="cpc" label="点击均价" sortable>
        </el-table-column>
        <el-table-column prop="cost" label="花费(元)" sortable>
        </el-table-column>
        <el-table-column key="adcreative_elements" label="预览图">
          <template slot-scope="scope"> 
            <div class="show-tuwen" v-if='scope.row.adcreative.adcreative_elements.hasOwnProperty("image_url")'> 
              <img :src="scope.row.adcreative.adcreative_elements.image_url" style="max-height: 200px; height: auto; width:100%;" />
            </div>
            <div class="show-tuwen" v-else-if='scope.row.adcreative.adcreative_elements.hasOwnProperty("element_story") && scope.row.adcreative.adcreative_elements.element_story[0].hasOwnProperty("image_url")'>
              <img :src="scope.row.adcreative.adcreative_elements.element_story[0].image_url" style="max-height: 200px; height: auto; width:100%;" />
            </div>
            <div class="show-tuwen" v-else>
            </div>
          </template>
        </el-table-column>
        <el-table-column key="system_status" label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.system_status=='AD_STATUS_DENIED'">
              <span style="margin-left:10px;color:red;">{{ system_status[scope.row.system_status] }}</span>
              <el-tooltip placement="bottom" effect="light">
                <div slot="content" style="width:200px;">
                  <p>{{ scope.row.reject_message }}</p>
                </div>
                <el-button type="text"><i class="el-icon-question"></i></el-button>
              </el-tooltip>
            </span>
            <span v-else>
              <span style="margin-left:10px">{{ system_status[scope.row.system_status] }}</span>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="configured_status" label="开关" sortable>
          <template slot-scope="scope">
            <el-switch @change="handleListStatusChange(scope.row)" v-model="scope.row.configured_status === 'AD_STATUS_NORMAL' ? true : false" active-color="#13ce66" inactive-color="#999">
            </el-switch>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="address" label="预警" sortable>
        </el-table-column> -->
        <el-table-column prop="address" label="操作">
          <template slot-scope="scope">
            <el-button @click="handleEditClick(scope.row.ad_id)" type="text" size="small">编辑</el-button>
            <el-button @click="handleCopyClick(scope.row.ad_id)" type="text" size="small">复制</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="table-pagination" v-if="page_info.total_number>0">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_info.page" :page-sizes="[20, 50, 100]" :page-size="page_info.page_size" layout="total, sizes, prev, pager, next, jumper" :total="page_info.total_number">
        </el-pagination>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  created: function() {
    //console.log(this.adsParams)
    if (this.account_id) {
      this.getAds(this.adsParams)
    }
    this.getAdCommongetEnum()
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.getAds(this.adsParams)
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      configured_status_command: '', //  批量状态绑定值
      configured_status_list: {}, // 批量状态选择
      multipleSelection: [], // 批量选择row
      adsParams: {
        page: 1,
        page_size: 20,
        ad_id: '',
        ad_name: '',
        campaign_id: '',
        configured_status: '',
        campaign_type: '',
        system_status: '',
        adgroup_id: '',
        report: 1,
        start_date: '',
        end_date: ''
      },
      loading: true,
      listData: [],
      page_info: {},
      system_status: {},
      ad_system_status: [{ value: '', label: '系统状态' }],
      campaign_typeList: [{ value: '', label: '广告类型' }],
      pickerDate: [new Date(), new Date()],
      pickerOptions2: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去7天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去30天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      value6: '',
      options: [
        {
          value: '0',
          label: '所有状态'
        },
        {
          value: '1',
          label: '暂停中'
        },
        {
          value: '2',
          label: '投放中'
        }
      ],
      value: '0'
    }
  },
  methods: {
    // 批量修改状态
    handleBatchChangeStatus() {
      var that = this
      var rows = that.multipleSelection
      if (rows && rows.length > 0) {
        var ids = []
        for (var i in rows) {
          ids.push(rows[i]['ad_id'])
        }
        if (!that.configured_status_command) {
          that.$message('请选择需要修改的状态')
          return false
        } else {
          var params = {
            ids: ids,
            configured_status: that.configured_status_command
          }
          that.handleBatchChangeStatusRequest(params) // 批量修改状态--请求
        }
      } else {
        that.$message('请选择需要修改的状态')
      }
    },
    // 批量修改状态--请求
    handleBatchChangeStatusRequest(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Ads/batchChange', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.getAds(that.adsParams) // 刷新广告列表
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    // 选择 状态
    handleCommand(command) {
      this.configured_status_command = command
    },
    // 批量删除广告
    handleAdListRemove() {
      var that = this
      var rows = that.multipleSelection
      if (rows && rows.length > 0) {
        var ids = []
        for (var i in rows) {
          ids.push(rows[i]['ad_id'])
        }
        this.$confirm('是否确认批量删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            var params = {
              ids: ids
            }
            that.adsBatchDelete(params) // 批量删除请求
          })
          .catch(() => {})
      } else {
        that.$message('请选择需要删除的记录')
      }
    },
    // 批量删除
    adsBatchDelete(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Ads/batchDelete', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          that.$message({
            type: 'success',
            message: respData.msg
          })
          if (respData.code == 0) {
            setTimeout(function() {
              that.getAds(that.adsParams) // 刷新广告列表
            }, 500)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    // 批量选择
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    // 开关
    handleListStatusChange(row) {
      var oldval = row.configured_status
      if (row.configured_status === 'AD_STATUS_NORMAL') {
        row.configured_status = 'AD_STATUS_SUSPEND'
      } else {
        row.configured_status = 'AD_STATUS_NORMAL'
      }
      this.setAdAdgroupsUpdate(
        row,
        'configured_status',
        row.configured_status,
        oldval
      )
    },
    // 开关状态 --  请求
    setAdAdgroupsUpdate(row, key, value, oldval) {
      var that = this
      var params = new Object()
      params['ad_id'] = row.ad_id
      params[key] = value
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Ads/update', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code != 0) {
            that.$message.error(respData.msg)
            row[key] = oldval
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          row[key] = oldval
        })
    },
    formatterSystemStatus(row, column) {
      return this.system_status[row.system_status]
    },
    unitConversion(row, column) {
      return row.cost / 100
    },
    // 新建广告
    newAd() {
      this.$router.push('/advertising/ad/new/')
    },
    // 分页
    handleSizeChange(val) {
      this.adsParams.page_size = val
      this.getAds(this.adsParams)
      // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      this.adsParams.page = val
      this.getAds(this.adsParams)
      //console.log(`当前页: ${val}`)
    },
    // 查询
    searchList() {
      this.adsParams.page = 1
      // this.adsParams.page_size = 10
      this.getAds(this.adsParams)
    },

    // 操作 -- 编辑
    handleEditClick(ad_id) {
      this.$router.push('/advertising/ad/edit/' + ad_id)
    },
    // 操作 -- 复制
    handleCopyClick(ad_id) {
      this.$router.push('/advertising/ad/copy/' + ad_id)
    },
    //导出execel
    exportExcel() {
      var params = this.adsParams
      window.location.href =
        '/Admin/Marketing_Ads/export?page=' +
        params.page +
        '&configured_status=' +
        params.configured_status +
        '&system_status=' +
        params.system_status +
        '&ad_name=' +
        params.ad_name +
        '&campaign_id =' +
        params.campaign_id +
        '&adgroup_id =' +
        params.adgroup_id +
        '&page_size	=' +
        params.page_size +
        '&start_date=' +
        params.start_date +
        '&end_date=' +
        params.end_date +
        '&account_id=' +
        this.account_id
    },
    //  日期选择
    pickerDateChange() {
      var start_date = new Date(this.pickerDate[0]),
        end_date = new Date(this.pickerDate[1])
      this.adsParams.start_date =
        start_date.getFullYear() +
        '-' +
        (start_date.getMonth() + 1) +
        '-' +
        start_date.getDate()
      this.adsParams.end_date =
        end_date.getFullYear() +
        '-' +
        (end_date.getMonth() + 1) +
        '-' +
        end_date.getDate()
      this.getAds(this.adsParams)
    },
    // 获取广告列表
    getAds: function(params) {
      var that = this
      that.loading = true
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Ads/get', {
          params: params
        })
        .then(function(response) {
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            // console.log('ad:', response)
            //console.log('respData.list:', respData.data.list)
            if (respData.data.list || respData.data.page_info) {
              that.listData = respData.data.list
              that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loading = false
        })
    },
    // 公共枚举
    getAdCommongetEnum() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            for (var key in respData.data['CAMPAIGN_TYPE']) {
              var obj = new Object()
              obj.value = key
              obj.label = respData.data['CAMPAIGN_TYPE'][key]['name']
              that.campaign_typeList.push(obj)
            }
            that.system_status = respData.data['AD_SYSTEM_STATUS']
            that.configured_status_list = respData.data['AD_STATUS']
            for (var key in respData.data['AD_SYSTEM_STATUS']) {
              var obj = new Object()
              obj.value = key
              obj.label = respData.data['AD_SYSTEM_STATUS'][key]
              that.ad_system_status.push(obj)
            }
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    }
  }
}
</script>


<style scoped>

</style>



